.hy-tab-item {
    width: 100px;
    display: flex;
    flex-direction: column;
    align-items: center;

    font-family:PingFangSC-Medium;
    font-weight:500;

    /* background: green; */
}

.hy-tab-item-selected {
    /* background: yellow; */
    width: 100px;
    display: flex;
    flex-direction: column;
    align-items: center;

    font-family:PingFangSC-Medium;
    font-weight:500;
    color: #3992F1;
}

.hy-tab-item:hover, .hy-tab-item:active {
