.hy-header {
    background: #fafafa;
    height: 112px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.logo {
    margin-left: 30px;
}

.logo img {
    width: 140px;
}

.content-img {
    width: 50%;
    float: left;
}

.content-img img {
    width: 100%;
}

.content-text {
    width: 40%;
    float: left;
    margin: 0px 5%;
}

.title-1 {
    font-size: 36px;
    font-family: PingFangSC-Semibold;
    font-weight: 600;
    color: rgba(17, 27, 52, 1);
    line-height:64px;
}

.title-2 {
    font-size: 30px;
    font-family: PingFangSC-Thin;
    font-weight: 100;
    color: rgba(17, 27, 52, 1);
    line-height: 38px;
}

.text-1 {
    font-size: 18px;
    font-family: PingFangSC-Light;
    font-weight: 300;
    color: rgba(17, 27, 52, 1);
    line-height: 28px;
}

.flex-row-center {
    display: flex;
    align-items: center;
}